// libraries
import { AxiosResponse } from 'axios';

// api
import apiRequest from 'api/restapi';

const addWish = (
  userId: string,
  name: string,
  price: string,
  description: string,
  image: string,
  wishListIds: string[],
): Promise<AxiosResponse<any>> =>
  apiRequest.post(`goods/addGood`, { userId, name, price, description, image, wishListIds });

export default { addWish };
