// libraries
import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';

// styled components
import { Frame, ColumnedFlex, Flex } from 'shared/styledComponents';
import { Text } from 'shared/typography';

// custom components
import Icon from 'components/Icon';
import AddWishForm from 'modules/AddWish';

// api
import api from './api';

// selectors
import { selectUser } from 'modules/User/selectors';

const AddPhoto = styled(ColumnedFlex)`
  align-items: center;
  margin-right: 20px;
  padding: 65px 0;
  max-width: 340px;
  max-height: 340px;

  width: 100%;
  height: 100%;
  background-color: ${({ theme: { colors } }): string => colors.lightGray};
`;

const AddPhotoText = styled(Text)`
  max-width: 170px;
  width: 100%;
  margin-top: 25px;
  font-weight: 600;
  color: ${({ theme: { colors } }): string => colors.nobel};
  font-size: 31px;
  text-align: center;
  line-height: 38px;
`;

const AddInfo = styled(ColumnedFlex)`
  width: 100%;
  height: 425px;
`;

const Wrapper = styled(Frame)`
  margin-top: 100px;
`;

const AddWishPage = () => {
  const { userId } = useSelector(selectUser);

  const onSubmit = useCallback(
    async (values: any) => {
      const { name, price, description, wishList } = values;

      await api.addWish(
        userId,
        name,
        price,
        description,
        'https://ae01.alicdn.com/kf/Hb921dbadcc4c4c5c9c3a198f2ad2b6aco.jpg',
        [wishList.value], // TODO:
      );
    },
    [userId],
  );

  return (
    <Wrapper>
      <Flex>
        <AddPhoto>
          <Icon type="addPhoto" width="160px" height="125px" />
          <AddPhotoText>Добавить фото</AddPhotoText>
        </AddPhoto>
        <AddInfo>
          <AddWishForm onSubmit={onSubmit} />
        </AddInfo>
      </Flex>
    </Wrapper>
  );
};

export default AddWishPage;
