// libraries
import React from 'react';
import { Link as ReactRouterLink, useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import styled from 'styled-components';

// styled components
import { Flex, Frame, Link } from 'shared/styledComponents';

// selectors
import { selectUser } from 'modules/User/selectors';

const Wrapper = styled(Flex)`
  height: 70px;
  margin-top: 80px;
  background-color: ${({ theme: { colors } }): string => colors.lightGray};
`;

const StyledLink = styled(Link)<any>(
  ({ theme: { colors }, isSelected }) => `
    padding: 17px 15px;
    font-style: normal;
    font-weight: 600;
    border-bottom: ${isSelected ? `4px solid ${colors.supernova}` : 'none'};

    &:hover {
      border-bottom: 4px solid ${colors.supernova};
    }
  `,
);

const Navigation = styled(Frame)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`;

const NavBar = () => {
  const location = useLocation();

  const { isLoggedIn } = useSelector(selectUser);

  return (
    <Wrapper>
      <Navigation as="nav">
        <StyledLink as={ReactRouterLink} to="/" isSelected={location.pathname === '/'} size="small">
          Идеи
        </StyledLink>
        <StyledLink as={ReactRouterLink} to="/" size="small">
          Категории
        </StyledLink>
        <StyledLink
          as={ReactRouterLink}
          to={isLoggedIn ? '/my-wishlist' : '/sign-in'}
          isSelected={location.pathname === '/my-wishlist'}
          size="small"
        >
          Мой вишлист
        </StyledLink>
        <StyledLink
          as={ReactRouterLink}
          to={isLoggedIn ? '/friends' : '/sign-in'}
          isSelected={location.pathname === '/friends'}
          size="small"
        >
          Друзья
        </StyledLink>
      </Navigation>
    </Wrapper>
  );
};

export default NavBar;
