// libraries
import React from 'react';
import { FormRenderProps } from 'react-final-form';
import styled from 'styled-components';

// custom components
import Form from 'components/Form';
import Input from 'components/Form/Input';
import FormSuccess from 'components/Form/FormSuccess';

// styled components
import { CentredFlex, SubmitButton, ColumnedFlex } from 'shared/styledComponents';

const Wrapper = styled(CentredFlex)`
  min-height: 500px;
  min-width: 500px;
`;

const FormContentWrapper = styled(ColumnedFlex)`
  align-items: flex-start;
  width: 100%;
`;

const InputsWrapper = styled(ColumnedFlex)`
  align-items: center;
  width: 100%;
  margin-bottom: 40px;
`;

const StyledInput = styled(Input)`
  width: 250px;
  height: 45px;
  margin-bottom: 10px;
  padding-left: 15px;
  border: 1px solid ${({ theme: { colors } }) => colors.supernova};
  border-radius: 15px;
  outline: none;
`;

const StyledButton = styled(SubmitButton)<any>`
  margin-left: 120px;
  font-weight: 900;
`;

interface Props {
  onSubmit: (values: any) => void;
  onSuccessComponentButtonClick: (formResetHandler: any) => () => void;
}

const SignUpForm = ({ onSubmit, onSuccessComponentButtonClick }: Props) => {
  return (
    <Wrapper>
      <Form onSubmit={onSubmit}>
        {({
          form: { reset },
          submitting,
          submitFailed,
          submitSucceeded,
          submitErrors,
        }: FormRenderProps): JSX.Element => (
          <FormContentWrapper>
            {submitSucceeded ? (
              <FormSuccess
                heading="Your account has been created successfully"
                onClose={onSuccessComponentButtonClick(reset)}
              />
            ) : (
              <>
                <InputsWrapper>
                  <StyledInput name="firstName" type="text" placeholder="Имя" />
                  <StyledInput name="lastName" type="text" placeholder="Фамилия" />
                  <StyledInput name="email" type="email" placeholder="Почта" />
                  <StyledInput name="password" type="password" placeholder="Пароль" />
                  <StyledInput name="city" type="city" placeholder="Город" />
                  <StyledInput name="country" type="country" placeholder="Страна" />
                </InputsWrapper>
                <StyledButton type="submit">регистрация</StyledButton>
              </>
            )}
          </FormContentWrapper>
        )}
      </Form>
    </Wrapper>
  );
};

export default SignUpForm;
