// libraries
import React, { useEffect, useState, useCallback } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

// custom components
import Home from './Home';

// api
import api from './api';
import favouritesApi from 'pages/Favourites/api';
import wishesApi from 'pages/WishList/api';

// modules
import { selectUser } from 'modules/User/selectors';

const HomePage = () => {
  const [isIdeasLoading, setIsIdeasLoading] = useState(false);

  const [allIdeas, setIdeas] = useState<any[] | null>(null);

  const [favourites, setFavourites] = useState<any[] | null>([]);

  const [wishes, setWishes] = useState<any[] | null>([]);

  const [currentIdeas, setCurrentIdeas] = useState<any[] | null>(null);

  const [paginationNumbers, setPaginationNumbers] = useState<number[]>([]);

  const [currentPage, setCurrentPage] = useState(1);

  const { isLoggedIn, userId } = useSelector(selectUser);

  const history = useHistory();

  const GOODS_PER_PAGE = 16;

  const handleChoosePage = useCallback(
    (pageNumber: number) => () => {
      if (!pageNumber) return;

      setCurrentPage(pageNumber);

      const indexOfLastGood = pageNumber * GOODS_PER_PAGE;
      const indexOfFirstGood = indexOfLastGood - GOODS_PER_PAGE;

      setCurrentIdeas(allIdeas?.slice(indexOfFirstGood, indexOfLastGood) || []);
    },
    [allIdeas],
  );

  const getIdeas = useCallback(async () => {
    setIsIdeasLoading(true);
    try {
      const ideas = await api.getIdeas();

      const ideaGoods = ideas.data.goods;

      setIdeas(ideaGoods);
      setCurrentIdeas(ideaGoods?.slice(0, GOODS_PER_PAGE) || []);

      const paginationAmount = Math.ceil(ideaGoods.length / GOODS_PER_PAGE);

      const paginationNumbersFromLoop = [];

      for (let i = 1; i <= paginationAmount; i++) {
        paginationNumbersFromLoop.push(i);
      }

      setPaginationNumbers(paginationNumbersFromLoop);

      if (isLoggedIn) {
        const allFavourites = await favouritesApi.getFavourites(userId);

        setFavourites(allFavourites.data?.goods || []);

        const allWishes = await wishesApi.getWishes(userId);

        setWishes(allWishes.data.goods);
      }
    } catch (err) {
      console.log('err', err);
    }
    setIsIdeasLoading(false);
  }, [isLoggedIn, userId]);

  useEffect(() => {
    getIdeas();
  }, [getIdeas]);

  const handleAddToFavourites = useCallback(
    (goodId: string) => async () => {
      if (isLoggedIn) {
        try {
          await api.addToFavourite(goodId, userId);

          const isAlreadyFavourite = favourites?.find((favourite: any) => favourite.id === goodId);

          if (isAlreadyFavourite) {
            setFavourites((state: any) => state.filter((favourite: any) => favourite.id !== goodId));
          } else {
            const allFavourites = await favouritesApi.getFavourites(userId);

            setFavourites(allFavourites.data.goods);
          }
        } catch (error) {
          console.log('err', error);
        }
      } else {
        history.push('/sign-in');
      }
    },
    [favourites, isLoggedIn, userId, history],
  );

  const handleAddToWish = useCallback(
    (goodId: string) => async () => {
      if (isLoggedIn) {
        try {
          const isAlreadyWish = wishes?.find((wish: any) => wish.id === goodId);

          if (isAlreadyWish) {
            await wishesApi.deleteWish(userId, goodId);

            setWishes((state: any) => state.filter((wish: any) => wish.id !== goodId));
          } else {
            await api.addToWish(goodId, userId);

            const allWishes = await wishesApi.getWishes(userId);

            setWishes(allWishes.data?.goods || []);
          }
        } catch (error) {
          console.log('err', error);
        }
      }
    },
    [wishes, isLoggedIn, userId],
  );

  return (
    <Home
      isIdeasLoading={isIdeasLoading}
      paginationNumbers={paginationNumbers}
      currentGoods={currentIdeas}
      currentPage={currentPage}
      handleChoosePage={handleChoosePage}
      handleAddToFavourites={handleAddToFavourites}
      handleAddToWish={handleAddToWish}
      wishes={wishes}
      favourites={favourites}
      allIdeas={allIdeas}
    />
  );
};

export default HomePage;
