// libraries
import { handleActions } from 'redux-actions';

// actions
import { setUser, setUserToken, clearUser } from './actions';

const defaultState = {
  userId: '',
  firstName: '',
  lastName: '',
  token: '',
  city: '',
  country: '',
  email: '',
  isLoggedIn: false,
};

const userReducer = handleActions(
  {
    [setUser.toString()]: (_, { payload: { userId, firstName, lastName, token, city, country, email } }: any) => ({
      userId,
      firstName,
      lastName,
      token,
      city,
      country,
      email,
      isLoggedIn: true,
    }),
    [setUserToken.toString()]: (state, { payload: token }: any) => ({
      ...state,
      token,
      isLoggedIn: true,
    }),
    [clearUser.toString()]: () => defaultState,
  },
  defaultState,
);

export default userReducer;
