// libraries
import React from 'react';
import styled from 'styled-components';

// styled components
import { Flex, ColumnedFlex, Frame } from 'shared/styledComponents';
import { Title, Text } from 'shared/typography';

// assets
import Ruslan from 'assets/images/ruslan.png';

const Wrapper = styled(Flex)`
  padding-bottom: 60px;
  margin-bottom: 60px;
  border-bottom: 1px solid #434343; //TODO:
`;

const Image = styled.img`
  width: 180px;
  height: 180px;
  margin-right: 60px;
`;

const InfoWrapper = styled(ColumnedFlex)`
  margin-right: 75px;
`;

const Name = styled(Title)`
  margin-bottom: 5px;
`;

const Location = styled(Text)`
  margin-bottom: 25px;
`;

const StyledButton = styled.button<any>`
  width: 180px;
  height: 50px;
  background: #ffc800;
  border-radius: 44px;
  font-style: normal;
  font-weight: 900;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: #ffffff;
  border: 0;
  cursor: pointer;
  outline: none;
`;

const ProductImage = styled.div`
  width: 195px;
  height: 180px;
  background-color: antiquewhite;

  &:not(:last-of-type) {
    margin-right: 15px;
  }
`;

interface FriendProps {
  name: string;
  location: string;
  id: string;
  handleOnClick?: any;
  isFollow?: boolean;
  image: string;
}

const FollowerFriend = ({ name, location, id, handleOnClick, isFollow = false, image }: FriendProps) => (
  <Frame>
    <Wrapper>
      <Image src={image || Ruslan} alt="Ruslan" />
      <InfoWrapper>
        <Name> {name}</Name>
        <Location size="small">{location}</Location>
        <StyledButton onClick={handleOnClick(id)}>{isFollow ? 'отписаться' : 'подписаться'}</StyledButton>
      </InfoWrapper>
      <Flex>
        <ProductImage />
        <ProductImage />
        <ProductImage />
        <ProductImage />
      </Flex>
    </Wrapper>
  </Frame>
);

export default FollowerFriend;
