// libraries
import React, { useMemo } from 'react';
import Select, { components } from 'react-select';
import { Field } from 'react-final-form';
import styled from 'styled-components';

import Icon from 'components/Icon';
import colors from 'theme/colors';

interface CustomSelectProps {
  name: string;
  options: any;
  customStyles?: any;
  isMulti?: boolean;
  dropdownIndicatorColor?: string;
  onSelectChange?: VoidFunction;
  isDisabled?: boolean;
}

const OptionIcon = styled(Icon)`
  margin-right: 27px;
`;

const DropdownIndicator = (props: any): JSX.Element => (
  <components.DropdownIndicator {...props}>
    <Icon type="downArrow" color={props.iconColor ? props.iconColor : colors.white} width="15px" height="7px" />
  </components.DropdownIndicator>
);

const CustomOption = (props: any) => (
  <components.Option {...props}>
    <OptionIcon type={props.isSelected ? 'checkedMark' : 'checkMark'} width="31px" height="31px" />
    {props.data.label}
  </components.Option>
);

const CustomSelect = ({
  name,
  options,
  customStyles,
  dropdownIndicatorColor,
  onSelectChange,
  isDisabled,
  ...props
}: CustomSelectProps & any): JSX.Element => {
  const selectComponents = useMemo(
    () => ({
      Option: CustomOption,
      DropdownIndicator: (props: any) => <DropdownIndicator iconColor={dropdownIndicatorColor} {...props} />,
    }),
    [dropdownIndicatorColor],
  );

  return (
    <Field name={name}>
      {({ input: { onChange, ...restInput }, ...rest }: any): JSX.Element => (
        <Select
          options={options}
          styles={customStyles}
          components={selectComponents}
          onChange={(e: any) => {
            onChange(e);

            if (onSelectChange) {
              onSelectChange(e);
            }
          }}
          isDisabled={isDisabled}
          {...restInput}
          {...rest}
          {...props}
        />
      )}
    </Field>
  );
};

export default CustomSelect;
