// Lib
import React from 'react';
import styled from 'styled-components';

// SVG icons
import { ReactComponent as Search } from 'assets/icons/search.svg';
import { ReactComponent as Plus } from 'assets/icons/plus.svg';
import { ReactComponent as Like } from 'assets/icons/like.svg';
import { ReactComponent as Profile } from 'assets/icons/profile.svg';
import { ReactComponent as Busket } from 'assets/icons/busket.svg';
import { ReactComponent as PrevArrow } from 'assets/icons/prevArrow.svg';
import { ReactComponent as NextArrow } from 'assets/icons/nextArrow.svg';
import { ReactComponent as AddToBasket } from 'assets/icons/addToBasket.svg';
import { ReactComponent as Favourites } from 'assets/icons/favourites.svg';
import { ReactComponent as Eye } from 'assets/icons/eye.svg';
import { ReactComponent as HandLike } from 'assets/icons/handLike.svg';
import { ReactComponent as AddNewList } from 'assets/icons/addNewList.svg';
import { ReactComponent as CloseCircle } from 'assets/icons/closeCircle.svg';
import { ReactComponent as AddPhoto } from 'assets/icons/addPhoto.svg';
import { ReactComponent as CheckMark } from 'assets/icons/checkMark.svg';
import { ReactComponent as CheckedMark } from 'assets/icons/checkedMark.svg';
import { ReactComponent as DownArrow } from 'assets/icons/downArrow.svg';
import { ReactComponent as InBasket } from 'assets/icons/inBasket.svg';

const Icons: any = {
  search: <Search />,
  plus: <Plus />,
  like: <Like />,
  profile: <Profile />,
  busket: <Busket />,
  prevArrow: <PrevArrow />,
  nextArrow: <NextArrow />,
  addToBasket: <AddToBasket />,
  favourites: <Favourites />,
  eye: <Eye />,
  handLike: <HandLike />,
  addNewList: <AddNewList />,
  closeCircle: <CloseCircle />,
  addPhoto: <AddPhoto />,
  checkMark: <CheckMark />,
  checkedMark: <CheckedMark />,
  downArrow: <DownArrow />,
  inBasket: <InBasket />,
};

const IconWrapper = styled.span<any>`
  display: flex;

  & i {
    display: inherit;
  }

  & svg {
    width: ${(props) => (props.width ? props.width : '25px')};
    height: ${(props) => (props.height ? props.height : '25px')};
    fill: ${({ color, theme: { colors } }) => (color ? color : colors.primary)};
  }
  & g {
    fill: ${({ color, theme: { colors } }) => (color ? color : colors.primary)};
  }
`;

interface IconProps {
  type: string;
  onClick?: VoidFunction;
  color?: string;
  width?: string;
  height?: string;
}

const Icon: React.FC<IconProps> = ({ type, onClick, color, width, height, ...props }): JSX.Element => (
  <IconWrapper color={color} width={width} height={height} onClick={onClick} {...props}>
    <i>{Icons[type]}</i>
  </IconWrapper>
);

export default Icon;
