// libraries
import React from 'react';
import styled from 'styled-components';

// styled components
import { Frame, Flex, CentredFlex, ColumnedFlex } from 'shared/styledComponents';
import { PaginationNumber } from 'shared/typography';

// custom components
import Icon from 'components/Icon';
import Loader from 'components/Loader';
import WishLists from 'components/WishLists';
import WishProduct from 'components/Product/WishProduct';

// hooks
import useModal from 'hooks/useModal';

const Wrapper = styled(ColumnedFlex)`
  margin-top: 40px;
`;

const Goods = styled(ColumnedFlex)`
  margin-top: 50px;
`;

const ProductsWrapper = styled(CentredFlex)`
  flex-wrap: wrap;
  justify-content: start;
`;

const PaginationWrapper = styled(Flex)`
  justify-content: space-between;
  max-width: 180px;
  margin: 80px auto;
`;

const StyledPaginationNumber = styled<any>(PaginationNumber)`
  margin-right: 20px;
  color: ${({ isChosen }) => (isChosen ? 'yellow' : 'black')}; // TODO:
  cursor: pointer;

  &:nth-of-type(n + 3) {
    margin-right: 0;
  }
`;

const SliderPrevArrow = styled(Icon)`
  margin-right: 20px;
  cursor: pointer;
`;

const SliderNextArrow = styled(Icon)`
  margin-left: 20px;
  cursor: pointer;
`;

const www = [
  // TODO:
  {
    name: 'fdsdfds',
    image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTasmxDhNB4rgukLa5D8hleFH92xV5bdaz9rg&usqp=CAU',
  },
  {
    name: 'fsamrhold',
    image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTasmxDhNB4rgukLa5D8hleFH92xV5bdaz9rg&usqp=CAU',
  },
];

interface WishListProps {
  isWishesLoading: boolean;
  paginationNumbers: number[];
  currentGoods: any[] | null;
  currentPage: number;
  handleChoosePage: any;
  handleDeleteWish: (id: string) => void;
}

const WishList = ({
  isWishesLoading,
  paginationNumbers,
  currentGoods,
  currentPage,
  handleChoosePage,
  handleDeleteWish,
}: WishListProps) => {
  const { handleModalOpen } = useModal();

  return (
    <Frame>
      <Wrapper>
        <WishLists handleModalOpen={handleModalOpen} wishLists={www} />
        <Goods>
          {isWishesLoading ? (
            <Loader />
          ) : (
            <>
              <ProductsWrapper>
                {currentGoods &&
                  currentGoods?.map((idea: any, i: any) => (
                    <WishProduct key={i} {...idea} handleDeleteWish={handleDeleteWish} />
                  ))}
              </ProductsWrapper>
              <PaginationWrapper>
                <SliderPrevArrow
                  type="prevArrow"
                  width="10px"
                  height="20px"
                  onClick={handleChoosePage(currentPage - 1)}
                />
                {paginationNumbers.map((i: number) => (
                  <StyledPaginationNumber isChosen={currentPage === i} key={i} onClick={handleChoosePage(i)}>
                    {i < 10 ? '0' + i : i}
                  </StyledPaginationNumber>
                ))}
                <SliderNextArrow
                  type="nextArrow"
                  width="10px"
                  height="20px"
                  onClick={handleChoosePage(currentPage + 1)}
                />
              </PaginationWrapper>
            </>
          )}
        </Goods>
      </Wrapper>
    </Frame>
  );
};

export default WishList;
