// libraries
import { AxiosResponse } from 'axios';

// api
import apiRequest from 'api/restapi';

export const postSignIn = (data: any): Promise<AxiosResponse<any>> => apiRequest.post('user/signIn', data);

export const postSignUp = (data: any): Promise<AxiosResponse<any>> => apiRequest.post('user/signUp', data);

export const postLogOut = (token: string): Promise<AxiosResponse<any>> =>
  apiRequest.post('user/logout', undefined, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });

export default { postSignIn, postSignUp, postLogOut };
