// libraries
import styled from 'styled-components';

// styles
import { BaseStyles, TitleStyles } from './styles';
import { Title } from './typography';

export const Flex = styled.div`
  display: flex;
`;

export const CentredFlex = styled(Flex)`
  justify-content: center;
  align-items: center;
`;

export const ColumnedFlex = styled(Flex)`
  flex-direction: column;
`;

export const Frame = styled.div`
  width: 100%;
  max-width: 1420px;
  margin: 0 auto;
`;

export const PrimaryButton = styled.button<any>(({ size, theme: { colors } }) => {
  switch (size) {
    case 'small':
      return `
        ${BaseStyles};
        width: fit-content;
        padding: 15px 60px;
        font-size: 12px;
        letter-spacing: 1.5px;
        text-transform: uppercase;
        font-style: normal;
        color: ${colors.white};
        background-color:  ${colors.supernova};
        border-radius: 44px;
      `;
    default:
      return `
        ${BaseStyles};
        width: fit-content;
        padding: 17px 60px;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        font-style: normal;
        color: ${colors.white};
        background-color:  ${colors.supernova};
        border-radius: 44px;
      `;
  }
});

export const Link = styled.a<any>`
  ${TitleStyles};
  text-decoration: none;
  cursor: pointer;
`;

export const SecondaryButton = styled(PrimaryButton)(
  ({ theme: { colors } }) => `
    padding: 0;
    color: ${colors.primary};
    background-color: ${colors.transparent};
  `,
);

export const SubmitButton = styled(PrimaryButton)(
  ({ isLoading }) => `
    cursor: ${isLoading ? 'wait' : 'pointer'};

    :disabled {
      cursor: not-allowed;
    }
  `,
);

export const StyledInput = styled.input<any>`
  ${BaseStyles};
  width: 300px;
  height: 45px;
  padding-left: 10px;
  border-bottom: 2px solid ${({ theme: { colors } }) => colors.black};
  font-size: 26px;
`;

export const Error = styled.p<any>`
  ${Title};
  margin-top: 20px;
  color: ${({ theme: { colors } }): string => colors.red};
`;

export const Label = styled.label<any>`
  ${TitleStyles};
`;
