// libraries
import React from 'react';
import styled from 'styled-components';
import { FormRenderProps } from 'react-final-form';

// custom components
import Form from 'components/Form';
import Input from 'components/Form/Input';
import Select from 'components/Form/Select';
import CustomTextarea from 'components/Form/Textarea';

// styled components
import { ColumnedFlex, SubmitButton, Error, Flex } from 'shared/styledComponents';
import { Text } from 'shared/typography';

// styles
import { TextStyles } from 'shared/styles';
import { MainSelectStyles } from 'modules/sharedStyles';

// constants
import { ACCESS_OPTIONS } from '../../../constants';

const FormContentWrapper = styled(ColumnedFlex)`
  align-items: flex-start;
  width: 100%;
`;

const InputsWrapper = styled(ColumnedFlex)`
  align-items: flex-end;
  width: 100%;
  margin-bottom: 40px;
`;

const StyledInput = styled(Input)`
  ${TextStyles};
  width: 100%;
  margin-bottom: 10px;
  margin-bottom: 25px;
  padding: 8px 30px;
  outline: none;
  font-weight: bold;
  border-radius: 62px;
  border: none;
  background-color: ${({ theme: { colors } }): string => colors.lightGray};

  ::placeholder {
    color: ${({ theme: { colors } }): string => colors.black};
  }
`;

const Label = styled(Text)`
  padding-right: 10px;
  font-weight: bold;
`;

const AccessWrapper = styled(Flex)`
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 25px;
`;

const StyledButton = styled(SubmitButton)<any>`
  margin-left: 120px;
  font-weight: 900;
`;

const StyledTextarea = styled(CustomTextarea)`
  ${TextStyles};
  width: 100%;
  height: 195px;
  padding: 25px 30px;
  background-color: ${({ theme: { colors } }): string => colors.lightGray};
  color: ${({ theme: { colors } }): string => colors.black};
  border-radius: 27px;
  border: none;
  resize: none;
  outline: none;

  ::placeholder {
    color: ${({ theme: { colors } }): string => colors.black};
    font-weight: bold;
  }
`;

interface AddWishListFormProps {
  onSubmit: any;
  handleChangeAccessRule: any;
}

const AddWishListForm = ({ onSubmit, handleChangeAccessRule }: AddWishListFormProps): JSX.Element => {
  return (
    <Form onSubmit={onSubmit}>
      {({ submitting, submitFailed, submitErrors }: FormRenderProps): JSX.Element => (
        <FormContentWrapper>
          <InputsWrapper>
            <StyledInput name="name" type="name" placeholder="Название" />
            <AccessWrapper>
              <Label>Кто может просматривать</Label>
              <Select
                name="accessRule"
                options={ACCESS_OPTIONS}
                customStyles={MainSelectStyles}
                onSelectChange={handleChangeAccessRule}
              />
            </AccessWrapper>
            <StyledTextarea name="description" placeholder="Описание" />
          </InputsWrapper>
          <StyledButton type="submit">сохранить</StyledButton>
          {submitFailed && <Error size="small">{submitErrors.error}</Error>}
        </FormContentWrapper>
      )}
    </Form>
  );
};

export default AddWishListForm;
