// libraries
import React from 'react';
import { Link as ReactRouterLink, useHistory } from 'react-router-dom';
import styled from 'styled-components';

// styled componentns
import { Flex, Frame, Link, StyledInput } from 'shared/styledComponents';
import { Text } from 'shared/typography';

//custom components
import Icon from './Icon';
import EllipseBackground from './EllipseBackground';

const Header = styled(Flex)`
  width: 100%;
  padding-top: 75px;
  background-color: ${({ theme: { colors } }): string => colors.white};
`;

const Wrapper = styled(Flex)`
  align-items: flex-end;
`;

const Logo = styled(Link)`
  font-style: normal;
  font-weight: 900;
  font-size: 40px;
  line-height: 49px;
  text-transform: uppercase;
`;

const StyledIcon = styled<any>(Icon)`
  align-items: flex-end;
  align-self: center;
  height: fit-content;
  margin-left: 50px;
  margin-right: 15px;
`;

const Input = styled(StyledInput)<any>`
  align-self: center;
  width: 265px;
`;
const AddWishWrapper = styled(Flex)<any>`
  align-items: inherit;
  cursor: pointer;
`;

const AddWishEllipse = styled.div`
  margin-left: 40px;
`;

const AddWish = styled(Text)`
  margin-left: 20px;
`;

const Balance = styled(Text)`
  width: fit-content;
  margin-left: 80px;
  margin-right: 35px;
`;

const IconWrapper = styled.div<any>`
  margin-left: 35px;
  cursor: pointer;
`;

const BusketIconWrapper = styled.div`
  position: relative;
  margin-left: 35px;
`;

const AmountOfGoods = styled(Text)`
  position: absolute;
  top: -15px;
  right: -15px;
  font-weight: bold;
`;

const HeaderComponent = (): JSX.Element => {
  const history = useHistory();

  const handleRedirect = (url: string) => () => {
    history.push(url);
  };

  return (
    <Header as="header">
      <Frame>
        <Wrapper>
          <Logo as={ReactRouterLink} to="/">
            wishmarket
          </Logo>
          <Flex>
            <StyledIcon type="search" width="31px" height="31px" />
            <Input type="text" width="31px" height="31px" />
          </Flex>

          <AddWishWrapper onClick={handleRedirect('/add-wish')}>
            <AddWishEllipse>
              <EllipseBackground>
                <Icon type="plus" width="31px" height="31px" />
              </EllipseBackground>
            </AddWishEllipse>
            <AddWish size="small">Добавить желание</AddWish>
          </AddWishWrapper>

          <Balance size="small">Баланс 12$</Balance>

          <Flex>
            <IconWrapper>
              <EllipseBackground>
                <Icon type="profile" width="31px" height="31px" />
              </EllipseBackground>
            </IconWrapper>
            <IconWrapper onClick={handleRedirect('/favourites')}>
              <EllipseBackground>
                <Icon type="like" width="31px" height="31px" />
              </EllipseBackground>
            </IconWrapper>
            <BusketIconWrapper>
              <EllipseBackground>
                <Icon type="busket" width="31px" height="31px" />
              </EllipseBackground>
              <AmountOfGoods>6</AmountOfGoods>
            </BusketIconWrapper>
          </Flex>
        </Wrapper>
      </Frame>
    </Header>
  );
};

export default HeaderComponent;
