// libraries
import { AxiosResponse } from 'axios';

// api
import apiRequest from 'api/restapi';

// constants
import { AccessTypes } from '../../constants';

const addWishList = (
  userId: string,
  name: string,
  accessRule: AccessTypes,
  description: string,
  image: string,
): Promise<AxiosResponse<any>> =>
  apiRequest.post(`wishList/createWishList`, { userId, name, accessRule, description, image });

export default { addWishList };
