// libraries
import React from 'react';
import styled from 'styled-components';

// styled components
import { ColumnedFlex } from 'shared/styledComponents';
import { Title, Text } from 'shared/typography';

const Wrapper = styled<any>(ColumnedFlex)(
  ({ isActive }) => `
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    margin: 0 80px;
    padding-top: 20px;
    flex-shrink: 0;
    overflow:hidden;
    ${
      isActive
        ? `
            align-items: center;
            width: 26%;
          `
        : ''
    }
  `,
);

const ImageWrapper = styled.img<any>(
  ({ isActive }) => `
    background-size: contain;
    max-width: 240px;
    width: 100%;
    height: 270px;
    margin-bottom: 25px;

    ${
      isActive
        ? `
    max-width: 330px;
          width: 100%;
          height: 390px;
          box-shadow: 0px 4px 27px rgba(0, 0, 0, 0.15);
        `
        : ''
    }
  `,
);

const Name = styled(Title)`
  width: fit-content;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 600;
`;

const Description = styled(Text)<any>`
  display: -webkit-box;
  max-width: 400px;
  width: 100%;
  margin-bottom: 10px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
`;

const Cost = styled(Title)<any>`
  width: fit-content;
  font-weight: 900;
  text-align: center;
`;

const WrapperDescription = styled(ColumnedFlex)`
  align-items: center;
`;

interface ProductProps {
  isActive: boolean;
  image: string;
  name: string;
  price: string;
  description: string;
}

const SliderProduct = ({ isActive, image, name, price, description }: ProductProps) => {
  return (
    <Wrapper isActive={isActive}>
      <ImageWrapper src={image} isActive={isActive} />
      {isActive && (
        <WrapperDescription>
          <Name>{name}</Name>
          <Description size="medium">{description}</Description>
          <Cost>{price} рублей</Cost>
        </WrapperDescription>
      )}
    </Wrapper>
  );
};

export default SliderProduct;
