import { css } from 'styled-components';

export const TitleStyles = css<any>(
  ({ theme: { colors, fonts } }: any) => `
      font-size: 28px;
      font-family: ${fonts.montserratLight};
      font-weight: normal;
      line-height: 34px;
      color: ${colors.black};
    `,
);

export const TextStyles = css<any>(({ size, theme: { colors, fonts } }) => {
  switch (size) {
    case 'small':
      return `
        font-size: 20px;
        font-style: normal;
        font-weight: normal;
        line-height: 24px;
        color: ${colors.black};
        font-family: ${fonts.montserratLight};
      `;
    case 'medium':
      return `
        font-size: 22px;
        font-weight: 600;
        line-height: 27px;
        color: ${colors.black};
        font-family: ${fonts.montserrat};
      `;
    default:
      return `
        font-size: 24px;
        font-weight: normal;
        line-height: 29px;
        color: ${colors.black};
        font-family: ${fonts.montserrat};
      `;
  }
});

export const BaseStyles = css`
  cursor: pointer;
  border: 0;
  outline: none;
  background-color: transparent;
  text-decoration: none;
`;
