// libraries
import React from 'react';
import styled from 'styled-components';

// custom components
import Icon from './Icon';

// styled components
import { ColumnedFlex, Flex } from 'shared/styledComponents';
import { Title } from 'shared/typography';

const ListItemWrapper = styled(ColumnedFlex)`
  cursor: pointer;

  &:not(:nth-child(n + 4)) {
    margin-right: 80px;
  }
`;

const Image = styled.img`
  width: 220px;
  height: 220px;
  margin-bottom: 30px;
  border-radius: 50%;
`;

const Name = styled(Title)`
  text-align: center;
  white-space: nowrap;
  font-weight: 900;
`;

const AddNewListItem = styled(Flex)`
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 220px;
  background: ${({ theme: { colors } }): string => colors.lightGray};
  border-radius: 50%;
  margin-bottom: 30px;
`;

interface ListItemProps {
  name: string;
  image?: string;
  isAddNew?: boolean;
  handleModalOpen?: any;
  isFriendList?: boolean;
}

const ListItem = ({ name, image, isAddNew, handleModalOpen, isFriendList }: ListItemProps) => {
  return (
    <ListItemWrapper>
      {isAddNew ? (
        <AddNewListItem onClick={handleModalOpen(isFriendList ? 'addFriendList' : 'addWishList')}>
          <Icon type="addNewList" width="70px" height="70px" />
        </AddNewListItem>
      ) : (
        <Image src={image} alt={`picture of a ${name} list`} />
      )}
      <Name>{name}</Name>
    </ListItemWrapper>
  );
};

export default ListItem;
