// libraries
import React from 'react';
import styled from 'styled-components';

// styles components
import { Flex, ColumnedFlex } from 'shared/styledComponents';
import { Title, Text } from 'shared/typography';

// custom components
import Icon from '../Icon';

// assets
import product from 'assets/images/product.png';

const Wrapper = styled(ColumnedFlex)`
  max-width: 340px;
  width: 100%;
  margin-top: 80px;
  margin-right: 20px;

  &:nth-child(4n) {
    margin-right: 0;
  }
`;

const ImageWrapper = styled.div`
  position: relative;
`;

const Image = styled.img`
  width: 340px;
  height: 355px;
  margin-bottom: 25px;
`;

const Favorites = styled(Flex)<any>(
  ({ isInFavourites }) => `
    position: absolute;
    top: 20px;
    right: 20px;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: ${isInFavourites ? 'rgba(0, 0, 0, 0.5)' : 'rgba(255, 255, 255, 0.5)'};
    cursor: pointer;

    &:hover {
      background: rgba(0, 0, 0, 0.5);
    }
  `,
);

const StyledIcon = styled(Icon)<any>(
  ({ isInFavourites, theme: { colors } }) => `
    padding: 10px;
    svg path {
      ${isInFavourites ? `stroke: ${colors.supernova};` : ''};
    }

    &:hover svg path {
      stroke: ${colors.supernova};
    }
  `,
);

const Name = styled(Text)`
  max-width: 265px;
  width: 100%;
  margin-bottom: 25px;
  margin-left: 45px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
`;

const BuyBlock = styled(Flex)`
  justify-content: space-between;
  width: 100%;
`;

const Cost = styled(Title)<any>`
  margin-left: 45px;
  font-weight: bold;
`;

const AddToBasket = styled(Icon)<any>`
  cursor: pointer;
`;

interface ProductProps {
  name?: string;
  description?: string;
  image?: string;
  price?: number;
  id: string;
  isInFavourites: boolean;
  isInWishes: boolean;
  handleAddToFavourites: (id: string) => void;
  handleAddToWish: (id: string) => void;
}

const Product = ({
  name = 'books with litehbvdds',
  description,
  image,
  price = 355,
  id,
  handleAddToFavourites,
  handleAddToWish,
  isInFavourites,
  isInWishes,
}: ProductProps) => {
  return (
    <Wrapper>
      <ImageWrapper>
        <Image src={image || product} />
        <Favorites onClick={handleAddToFavourites(id)} isInFavourites={isInFavourites}>
          <StyledIcon type="favourites" isInFavourites={isInFavourites} />
        </Favorites>
      </ImageWrapper>
      <Name>{name}</Name>
      <BuyBlock>
        <Cost>{price} рублей</Cost>
        <AddToBasket
          width="31px"
          height="31px"
          type={isInWishes ? 'inBasket' : 'addToBasket'}
          onClick={handleAddToWish(id)}
        />
      </BuyBlock>
    </Wrapper>
  );
};

export default Product;
