// libraries
import styled from 'styled-components';

// styles
import { TitleStyles } from './styles';

export const H1 = styled.h1(
  ({ theme: { colors, fonts } }) => `
    font-size: 50px;
    font-weight: bold;
    line-height: 61px;
    font-family: ${fonts.montserrat};
    color: ${colors.primary};
  `,
);

export const H2 = styled.h2(
  ({ theme: { devices } }) => `
    ${TitleStyles};

    @media ${devices.mobile} {
      font-size: 36px;
      line-height: 42px;
    }
  `,
);

export const Title = styled.p<any>`
  ${TitleStyles};
`;

export const Text = styled.p<any>(({ size, theme: { colors, fonts } }) => {
  switch (size) {
    case 'small':
      return `
        font-size: 20px;
        font-style: normal;
        font-weight: normal;
        line-height: 24px;
        color: ${colors.black};
        font-family: ${fonts.montserratLight};
      `;
    case 'medium':
      return `
        font-size: 22px;
        font-weight: 600;
        line-height: 27px;
        color: ${colors.black};
        font-family: ${fonts.montserrat};
      `;
    default:
      return `
        font-size: 24px;
        font-weight: normal;
        line-height: 29px;
        color: ${colors.black};
        font-family: ${fonts.montserrat};
      `;
  }
});

export const PaginationNumber = styled.p(
  ({ theme: { colors, fonts } }) => `
    font-size: 26px;
    font-weight: normal;
    line-height: 32px;
    font-family: ${fonts.montserrat};
    color: ${colors.black};
  `,
);
