// libraries
import { AxiosResponse } from 'axios';

// api
import apiRequest from 'api/restapi';

// constants
import { AccessTypes } from '../../constants';

const getFollowers = (userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.get(`user/getFollowers?userId=${userId}`);

const getFollowing = (userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.get(`user/getFollowing?userId=${userId}`);

const addToFollowing = (userId: string, followingId: string): Promise<AxiosResponse<any>> =>
  apiRequest.post(`user/addToFollowing`, { userId, followingId });

const addFriendList = (
  userId: string,
  name: string,
  accessRule: AccessTypes,
  description: string,
  image: string,
  accessUsers: any[],
): Promise<AxiosResponse<any>> =>
  apiRequest.post(`friendsList/createFriendList`, { userId, name, accessRule, description, image, accessUsers });

const getFriendLists = (userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.get(`friendsList/getAllFriendListsByUserId?userId=${userId}`);

const getUsersByName = (name: string, userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.get(`user/getUsersByName?name=${name}&userId=${userId}`);

export default { getFollowers, getFollowing, addToFollowing, addFriendList, getFriendLists, getUsersByName };
