// libraries
import React, { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';

//custom components
import SignInForm from './SignIn';

// api
import api from '../User/api';

// actions
import { setUser } from 'modules/User/actions';

// services
import { convertUserDataToViewModel } from './services';

const SignUpFormContainer = () => {
  const dispatch = useDispatch();

  const history = useHistory();

  const onSubmit = useCallback(
    async (values: any) => {
      try {
        const user = await api.postSignIn(values);

        const convertedUser = convertUserDataToViewModel(user.data);

        dispatch(setUser(convertedUser));

        history.push('/home');
      } catch (err) {
        console.log('error', err);
      }
    },
    [dispatch, history],
  );

  return <SignInForm onSubmit={onSubmit} />;
};

export default SignUpFormContainer;
