// libraries
import React, { ReactElement, useCallback } from 'react';
import styled from 'styled-components';

// styled components
import { ColumnedFlex, Frame, Flex, StyledInput } from 'shared/styledComponents';
import { Title } from 'shared/typography';

// custom components
import Icon from 'components/Icon';
import FollowerFriend from 'components/Friend/FollowerFriend';
import FollowingFriend from 'components/Friend/FollowingFriend';
import FriendList from 'components/FriendList';

// types
import { FriendTypes } from 'types';

const Wrapper = styled(ColumnedFlex)`
  margin-top: 40px;
`;

const NavigationBar = styled(Flex)`
  justify-content: space-between;
  width: 100%;
  margin-bottom: 75px;
`;

const PanelsWrapper = styled(Flex)`
  justify-content: space-between;
  max-width: 490px;
  width: 100%;
`;

const Panel = styled(Title)<any>(
  ({ theme: { colors }, isOpened }) => `
    min-height: 55px;
    height: 100%;
    padding-bottom: 10px;
    cursor: pointer;
    border-bottom: ${isOpened ? `4px solid ${colors.supernova}` : 'none'};

    &:hover {
      border-bottom: 4px solid ${colors.supernova};
    }
  `,
);

const Followers = styled(ColumnedFlex)`
  width: 100%;
`;

const StyledIcon = styled<any>(Icon)`
  align-items: flex-end;
  align-self: center;
  height: fit-content;
  margin-left: 50px;
  margin-right: 15px;
`;

const Input = styled(StyledInput)<any>`
  align-self: center;
`;

const Following = styled(ColumnedFlex)`
  width: 100%;
`;

interface FriendProps {
  handleChangePenel: any;
  openedPanel: FriendTypes;
  currentUser: any;
  handleOnFollowerClick: any;
  isAddFriendLoading: boolean;
  handleModalOpen: any;
  friends: any[];
  friendLists: any[];
  handleOnFollowingClick: any;
  handleFindFriends: any;
  findFriendData: string;
  handleFindFollowFriend: any;
}

const Friends = ({
  handleChangePenel,
  openedPanel,
  friends,
  currentUser,
  handleOnFollowerClick,
  isAddFriendLoading,
  handleModalOpen,
  friendLists,
  handleFindFriends,
  findFriendData,
  handleOnFollowingClick,
  handleFindFollowFriend,
}: FriendProps): ReactElement => {
  const handleOnChange = useCallback(
    (e: any) => {
      switch (openedPanel) {
        case FriendTypes.Following:
          return handleFindFollowFriend(e);
        case FriendTypes.Follower:
          return handleFindFollowFriend(e);
        case FriendTypes.Find:
          return handleFindFriends(e);
      }
    },
    [openedPanel, handleFindFollowFriend, handleFindFriends],
  );

  return (
    <Frame>
      <Wrapper>
        <NavigationBar>
          <PanelsWrapper>
            <Panel onClick={handleChangePenel(FriendTypes.Follower)} isOpened={openedPanel === FriendTypes.Follower}>
              Подписчики
            </Panel>
            <Panel onClick={handleChangePenel(FriendTypes.Following)} isOpened={openedPanel === FriendTypes.Following}>
              Подписки
            </Panel>
            <Panel onClick={handleChangePenel(FriendTypes.Find)} isOpened={openedPanel === FriendTypes.Find}>
              Поиск
            </Panel>
          </PanelsWrapper>
          <Flex>
            <StyledIcon type="search" width="31px" height="31px" />
            <Input onChange={handleOnChange} value={findFriendData} />
          </Flex>
        </NavigationBar>
        {openedPanel === FriendTypes.Follower && (
          <Followers>
            {friends.map((friend: any) => (
              <FollowerFriend
                key={friend.id}
                id={friend.id}
                image={friend.image}
                name={friend.firstName + ' ' + friend.lastName}
                location={friend.city + ', ' + friend.country}
                isFollow={currentUser && currentUser.following.length && currentUser.following?.includes(friend.id)}
                handleOnClick={handleOnFollowerClick}
              />
            ))}
          </Followers>
        )}
        {openedPanel === FriendTypes.Following && (
          <Following>
            <FriendList handleModalOpen={handleModalOpen} friendLists={friendLists} />
            {friends.map((friend: any) => (
              <FollowingFriend
                key={friend.id}
                image={friend.image}
                id={friend.id}
                name={friend.firstName + ' ' + friend.lastName}
                location={friend.city + ', ' + friend.country}
                handleOnClick={handleOnFollowingClick}
              />
            ))}
          </Following>
        )}
        {openedPanel === FriendTypes.Find && (
          <>
            {!friends || !friends.length ? (
              <div>i'm sorry, no users found</div>
            ) : (
              <Followers>
                {friends.map((friend: any) => (
                  <FollowerFriend
                    key={friend.id}
                    id={friend.id}
                    image={friend.image}
                    name={friend.firstName + ' ' + friend.lastName}
                    location={friend.city + ', ' + friend.country}
                    isFollow={currentUser && currentUser.following.length && currentUser.following?.includes(friend.id)}
                    handleOnClick={handleOnFollowerClick}
                  />
                ))}
              </Followers>
            )}
          </>
        )}
      </Wrapper>
    </Frame>
  );
};

export default Friends;
