// libraries
import { css, FlattenSimpleInterpolation } from 'styled-components';

// styles
import fonts from 'theme/fonts';
import colors from 'theme/colors';

// selectors styles
const SelectTextStyles = css`
  font-size: 24px;
  font-weight: normal;
  line-height: 29px;
  color: ${colors.black};
  font-family: ${fonts.montserrat};
`;

const List = css`
  max-height: 450px;
  overflow-y: auto;
  border: none;
  border-radius: 27px;

  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: ${colors.lightGray};
  }
`;

export const MainSelectStyles = {
  control: (provided: FlattenSimpleInterpolation, { menuIsOpen }: any): FlattenSimpleInterpolation =>
    css`
      ${provided};
      min-height: 25px;
      min-width: 400px;
      padding: 8px 30px;
      border: none;
      background-color: ${colors.lightGray};
      box-shadow: none;
      cursor: pointer;
      border-radius: 62px;
      ${menuIsOpen ? 'border-bottom-right-radius: 0px' : ''};
      ${menuIsOpen ? 'border-bottom-left-radius: 0px' : ''};
    `,
  option: (provided: FlattenSimpleInterpolation, state: any): FlattenSimpleInterpolation => css`
    ${provided};
    color: ${colors.supernova};
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    font-size: 28px;
    font-family: ${fonts.montserratLight};
    font-weight: normal;
    line-height: 34px;
    color: black;
    cursor: pointer;
    background-color: ${state.isSelected ? colors.lightGray : colors.lightGray};

    :hover {
      background-color: ${colors.lightGray};
    }
  `,
  input: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation => css`
    ${provided};
    ${SelectTextStyles};
  `,
  valueContainer: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation =>
    css`
      ${provided};
      padding: 0;
    `,
  placeholder: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation => css`
    ${provided};
    ${SelectTextStyles};
  `,
  singleValue: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation =>
    css`
      ${provided};
      ${SelectTextStyles};
    `,
  menuList: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation =>
    css`
      ${provided};
      ${SelectTextStyles};
      ${List}
      width: 100%;
      padding: 15px 30px 20px;
      font-size: 16px;
      line-height: 24px;
      background-color: ${colors.lightGray};
      border: none;
      box-shadow: none;
    `,
  menu: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation =>
    css`
      ${provided};
      ${SelectTextStyles};
      margin: 0;
      background-color: ${colors.lightGray};
      border: none;
      border-bottom-right-radius: 27px;
      border-bottom-left-radius: 27px;
      box-shadow: none;
    `,
  indicatorSeparator: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation =>
    css`
      ${provided};
      display: none;
    `,
  dropdownIndicator: (provided: FlattenSimpleInterpolation): FlattenSimpleInterpolation =>
    css`
      ${provided};
      padding: 0;

      & g {
        /* fill: ${colors.black}; */
      }
    `,
};
