// libraries
import React, { useState, useCallback, useEffect } from 'react';
import { useSelector } from 'react-redux';

// custom components
import Friends from './Friends';

// types
import { FriendTypes } from 'types';

// hooks
import useModal from 'hooks/useModal';

// api
import api from './api';
import userApi from 'api/user';

// selectors
import { selectUser } from 'modules/User/selectors';

const FriendsContainer = () => {
  const [openedPanel, setOpenedPanel] = useState(FriendTypes.Follower);

  const [friends, setFriends] = useState([]) as any;

  const [sortedFriends, setSortedFriends] = useState([]) as any;

  const [isAddFriendLoading, setIsAddFriendLoading] = useState(false);

  const [findFriendData, setFindFriendData] = useState('');

  const [friendLists, setFriendLists] = useState([]) as any;

  const [currentUser, setCurrentUser] = useState(null) as any;

  const { userId } = useSelector(selectUser);

  const getCurrentUser = useCallback(async () => {
    const user = await userApi.getCurrentUser(userId);

    setCurrentUser(user.data);
  }, [setCurrentUser, userId]);

  const getFriendLists = useCallback(async () => {
    const friendList = await api.getFriendLists(userId);

    setFriendLists(friendList.data);
  }, [setFriendLists, userId]);

  const handleFindFriends = useCallback(
    async (e: any) => {
      if (openedPanel !== FriendTypes.Find) return;

      setFindFriendData(e.target.value);

      const users = await api.getUsersByName(e.target.value, userId);

      setSortedFriends(users.data);
    },
    [openedPanel, userId, setSortedFriends],
  );

  const handleFindFollowFriend = useCallback(
    async (e: any) => {
      const name = e.target.value.toLowerCase();

      if (!name) {
        setFindFriendData(name);
        setSortedFriends(friends);

        return;
      }

      const isFirstAndLast = name.trim().split(' ').length === 2;

      const [first, last] = isFirstAndLast ? name.trim().split(' ') : [];

      setFindFriendData(name);

      const sortedUsers = isFirstAndLast
        ? friends.filter(
            (friend: any) => friend.firstName.toLowerCase() === first && friend.lastName.toLowerCase().includes(last),
          )
        : friends.filter(
            (friend: any) =>
              friend.firstName.toLowerCase().includes(name.trim()) ||
              friend.lastName.toLowerCase().includes(name.trim()),
          );

      setSortedFriends(sortedUsers);
    },
    [friends, setSortedFriends],
  );
  const getFollowers = useCallback(async () => {
    const followers = await api.getFollowers(userId);

    setFriends(followers.data);
  }, [setFriends, userId]);

  const handleChangePenel = useCallback(
    (friendType: FriendTypes) => async () => {
      if (friendType === FriendTypes.Follower) {
        const followers = await api.getFollowers(userId);

        setFriends(followers.data);
        setSortedFriends(followers.data);
      } else if (friendType === FriendTypes.Following) {
        const following = await api.getFollowing(userId);

        setFriends(following.data);
        setSortedFriends(following.data);
      } else if (friendType === FriendTypes.Find) {
        const users = await api.getUsersByName('', userId);

        setSortedFriends(users.data);
      }

      setOpenedPanel(friendType);
    },
    [setSortedFriends, userId, setFriends],
  );

  useEffect(() => {
    getCurrentUser();
    getFriendLists();
    getFollowers();
    handleChangePenel(FriendTypes.Follower)();
  }, [getCurrentUser, getFriendLists, getFollowers, handleChangePenel]);

  const { handleModalOpen } = useModal();

  const handleOnFollowerClick = useCallback(
    (id: string) => async () => {
      setIsAddFriendLoading(true);
      try {
        await api.addToFollowing(userId, id);

        const isAlreadyFollowing = currentUser.following.includes(id);

        if (isAlreadyFollowing) {
          setCurrentUser((state: any) => ({
            ...state,
            following: state.following.filter((item: any) => item !== id),
          }));
        } else {
          setCurrentUser((state: any) => ({ ...state, following: [...state.following, id] }));
        }
      } catch (err) {
        console.log('err', err);
      }
      setIsAddFriendLoading(false);
    },
    [currentUser, setCurrentUser, userId],
  );

  const handleOnFollowingClick = () => () => {
    console.log('err');
  };

  return (
    <Friends
      handleChangePenel={handleChangePenel}
      openedPanel={openedPanel}
      friends={sortedFriends}
      currentUser={currentUser}
      handleOnFollowerClick={handleOnFollowerClick}
      isAddFriendLoading={isAddFriendLoading}
      handleModalOpen={handleModalOpen}
      friendLists={friendLists}
      handleOnFollowingClick={handleOnFollowingClick}
      handleFindFriends={handleFindFriends}
      findFriendData={findFriendData}
      handleFindFollowFriend={handleFindFollowFriend}
    />
  );
};

export default FriendsContainer;
