// libraries
import React, { useEffect, useState, useCallback } from 'react';
import { useSelector } from 'react-redux';

// custom components
import Favourites from './Favourites';

// api
import api from './api';

// selectors
import { selectUser } from 'modules/User/selectors';

const FavouritesPage = () => {
  const [isFavouritesLoading, setIsFavouritesLoading] = useState(false);

  const [allFavourites, setFavourites] = useState<any[] | null>(null);

  const [currentFavourites, setCurrentFavourites] = useState<any[] | null>(null);

  const [paginationNumbers, setPaginationNumbers] = useState<number[]>([]);

  const [currentPage, setCurrentPage] = useState(1);

  const GOODS_PER_PAGE = 16;

  const { userId } = useSelector(selectUser);

  const handleChoosePage = useCallback(
    (pageNumber: number) => () => {
      if (!pageNumber) return;

      setCurrentPage(pageNumber);

      const indexOfLastGood = pageNumber * GOODS_PER_PAGE;
      const indexOfFirstGood = indexOfLastGood - GOODS_PER_PAGE;

      setCurrentFavourites(allFavourites?.slice(indexOfFirstGood, indexOfLastGood) || []);
    },
    [allFavourites],
  );

  const getFavourites = useCallback(async () => {
    setIsFavouritesLoading(true);

    try {
      const favouritesData = await api.getFavourites(userId);

      setFavourites(favouritesData.data.goods);
      setCurrentFavourites(favouritesData.data.goods?.slice(0, GOODS_PER_PAGE) || []);

      const paginationAmount = Math.ceil(40 / GOODS_PER_PAGE);

      for (let i = 1; i <= paginationAmount; i++) {
        setPaginationNumbers((state: number[]) => [...state, i]);
      }
    } catch (err) {
      console.log('err', err);
    }

    setIsFavouritesLoading(false);
  }, [userId]);

  useEffect(() => {
    getFavourites();
  }, [getFavourites]);

  return (
    <Favourites
      isFavouritesLoading={isFavouritesLoading}
      paginationNumbers={paginationNumbers}
      currentFavourites={currentFavourites}
      currentPage={currentPage}
      handleChoosePage={handleChoosePage}
    />
  );
};

export default FavouritesPage;
