// libraries
import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';

// hooks
import useModal from 'hooks/useModal';

// custom components
import Icon from 'components/Icon';
import AddWishListForm from './components/AddWishList';

// styled components
import { Frame, ColumnedFlex, Flex } from 'shared/styledComponents';
import { H1, Text } from 'shared/typography';

// api
import api from './api';

// selectors
import { selectUser } from 'modules/User/selectors';

const Wrapper = styled(Frame)`
  position: absolute;
  top: 120px;
  height: 80%;
  padding: 80px;
  overflow: auto;
  background-color: ${({ theme: { colors } }): string => colors.white};
  outline: none;
  font-weight: bold;
`;

const AddWishListWrapper = styled(ColumnedFlex)`
  max-width: 1250px;
  width: 100%;
  padding-top: 80px 120px 100px;
`;

const Header = styled(Flex)`
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
`;

const StyledIcon = styled(Icon)<any>`
  height: fit-content;
  width: fit-content;
  cursor: pointer;
`;

const AddPhoto = styled(ColumnedFlex)`
  align-items: center;
  margin-right: 20px;
  padding: 65px 0;
  max-width: 340px;
  max-height: 340px;

  width: 100%;
  height: 100%;
  background-color: ${({ theme: { colors } }): string => colors.lightGray};
`;

const AddPhotoText = styled(Text)`
  max-width: 170px;
  width: 100%;
  margin-top: 25px;
  font-weight: 600;
  color: ${({ theme: { colors } }): string => colors.nobel};
  font-size: 31px;
  text-align: center;
  line-height: 38px;
`;

const AddInfo = styled(ColumnedFlex)`
  width: 100%;
  height: 425px;
`;

const AddWishList = () => {
  const { userId } = useSelector(selectUser);

  const { handleModalClose } = useModal();

  const handleChangeAccessRule = (e: any) => {
    // TODO:
    // console.log('ee', e);
  };

  const onSubmit = useCallback(
    async ({ name, description, accessRule }: any) => {
      await api.addWishList(
        userId,
        name,
        accessRule.value,
        description,
        'https://bigpikcha.ru/wp-content/uploads/2019/03/mai.jpg', // TODO:
      );
    },
    [userId],
  );

  return (
    <Wrapper>
      <AddWishListWrapper>
        <Header>
          <H1>Добавить список</H1>
          <StyledIcon type="closeCircle" width="50px" height="50px" onClick={handleModalClose} />
        </Header>

        <Flex>
          <AddPhoto>
            <Icon type="addPhoto" width="160px" height="125px" />
            <AddPhotoText>Добавить фото</AddPhotoText>
          </AddPhoto>
          <AddInfo>
            <AddWishListForm onSubmit={onSubmit} handleChangeAccessRule={handleChangeAccessRule} />
          </AddInfo>
        </Flex>
      </AddWishListWrapper>
    </Wrapper>
  );
};

export default AddWishList;
