// libraries
import React, { useState, useEffect } from 'react';
// import Slider from 'react-slick';
import styled from 'styled-components'; // TODO:

// styles
import 'shared/sliderStyles.css';
import './slider.css';

// custom components
import SliderProduct from 'components/SliderProduct';
import Icon from 'components/Icon';

const initStateSlider = {
  current: 1,
  translateX: `translateX(0)`,
  translateValue: 0,
};

interface PopularContainerProps {
  allIdeas?: any[] | null;
}

const PopularContainer = ({ allIdeas = [] }: PopularContainerProps) => {
  const [stateSlider, setStateSlider] = useState(initStateSlider);

  const [dataSlider, setDataSlider] = useState<any[] | null>(null);

  const [isDisabledNextBtn, setIsDisabledNextBtn] = useState(false);

  const [isDisabledPrevBtn, setIsDisabledPrevBtn] = useState(false);

  const toggleStateSlider = ({ stateCurrent, step }: { stateCurrent: number; step: number }) => {
    let { current, translateX, translateValue } = stateSlider; // разбили состояние слайдера

    const sliderDataLength = dataSlider && dataSlider.length - 1; // получили колличество всех элементов слайдера

    current += stateCurrent; // изменили индекмс активного слайдера

    // if (sliderDataLength == current && !isDisabledNextBtn) setIsDisabledNextBtn(true);
    // // проверяем нужно ли делать disable кнопке next
    // else if (isDisabledNextBtn) setIsDisabledNextBtn(false);

    if (sliderDataLength === current) {
      setIsDisabledNextBtn(!isDisabledNextBtn);
    }
    // // проверяем нужно ли делать disable кнопке next
    else if (isDisabledNextBtn) setIsDisabledNextBtn(false);

    if (!current && !isDisabledPrevBtn) setIsDisabledPrevBtn(true);
    // проверяем нужно ли делать disable кнопке prev
    else if (isDisabledPrevBtn) setIsDisabledPrevBtn(false);

    translateValue = translateValue + step; // увеличиваем
    translateX = `translateX(${translateValue}px)`;

    setStateSlider({ current, translateX, translateValue });
  };

  useEffect(() => {
    setDataSlider(allIdeas);
  }, [allIdeas]);

  if (!dataSlider || !dataSlider.length) return <div>Loading...</div>;

  return (
    <Wrapper>
      <WrapperSlider className="slider" translateX={stateSlider.translateX}>
        {dataSlider.map((item, idx) => (
          <SliderProduct key={item.id} isActive={idx === stateSlider.current} {...item} />
        ))}
      </WrapperSlider>
      {!isDisabledPrevBtn && (
        <ArrowPrew
          onClick={() => toggleStateSlider({ stateCurrent: -1, step: 441 })}
          type="prevArrow"
          height={15}
          width={15}
        />
      )}
      {!isDisabledNextBtn && (
        <ArrowNext
          onClick={() => toggleStateSlider({ stateCurrent: +1, step: -441 })}
          type="nextArrow"
          height={'15px'}
          width={'15px'}
        />
      )}
    </Wrapper>
  );
};

const Wrapper = styled.div`
  position: relative;
  overflow: hidden;
  min-height: 600px;
`;

const WrapperSlider = styled.div<any>`
  display: flex;
  position: relative;
  align-items: center;
  transform: ${({ translateX }): string => translateX};
`;

const ArrowPrew = styled(Icon)<any>`
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 43%;
  left: 0;
  background: #ffc800;
  border-radius: 50%;
  width: 35px;
  height: 35px;
`;

const ArrowNext = styled(Icon)<any>`
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 43%;
  right: 0;
  background: #ffc800;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background-size: contain;
`;

export default PopularContainer;
