// libraries
import React from 'react';
import { Field } from 'react-final-form';

interface CustomTextareaProps {
  name: string;
  customStyles?: any;
  onTextareaChange?: VoidFunction;
  placeholder: string;
}

const CustomTextarea = ({
  name,
  customStyles,
  onTextareaChange,
  placeholder,
  ...props
}: CustomTextareaProps & any): JSX.Element => {
  return (
    <Field name={name}>
      {({ input: { onChange, ...restInput }, ...rest }: any): JSX.Element => (
        <textarea
          onChange={(e: any) => {
            onChange(e);

            if (onTextareaChange) {
              onTextareaChange(e);
            }
          }}
          placeholder={placeholder}
          {...restInput}
          {...rest}
          {...props}
        />
      )}
    </Field>
  );
};

export default CustomTextarea;
