// libraries
import React, { useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';

// selectors
import { selectUser } from 'modules/User/selectors';

// custom components
import AddWishForm from './AddWishForm';

// api
import wishListApi from 'pages/WishList/api';

interface AddWishListFormProps {
  onSubmit: any;
}

const AddWishListForm = ({ onSubmit }: AddWishListFormProps): JSX.Element => {
  const { userId } = useSelector(selectUser);

  const [wishListOptions, setWishListOptions] = useState([]);

  const getWishLists = useCallback(async () => {
    const wishLists = await wishListApi.getAllWishLists(userId);

    const convertedWishLists = wishLists.data.map((list: any) => ({
      label: list.name,
      value: list.id,
    }));

    setWishListOptions(convertedWishLists);
  }, [userId]);

  useEffect(() => {
    getWishLists();
  }, [getWishLists]);

  return <AddWishForm onSubmit={onSubmit} wishListOptions={wishListOptions} />;
};

export default AddWishListForm;
