// libraries
import React, { useState, useCallback, useEffect } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';

// hooks
import useModal from 'hooks/useModal';

// custom components
import Icon from 'components/Icon';
import AddFriendListForm from './components/AddFriendList';

// styled components
import { Frame, ColumnedFlex, Flex, StyledInput } from 'shared/styledComponents';
import { H1, Text, Title } from 'shared/typography';

// api
import api from 'pages/Friends/api';

// modules
import { selectUser } from 'modules/User/selectors';

const Wrapper = styled(Frame)`
  position: absolute;
  top: 120px;
  height: 80%;
  padding: 80px;
  overflow: auto;
  background-color: ${({ theme: { colors } }): string => colors.white};
  outline: none;
  font-weight: bold;
`;

const AddFriendListWrapper = styled(ColumnedFlex)`
  max-width: 1250px;
  width: 100%;
  padding-top: 80px 120px 100px;
`;

const Header = styled(Flex)`
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
`;

const StyledIcon = styled(Icon)<any>`
  height: fit-content;
  width: fit-content;
  cursor: pointer;
`;

const AddPhoto = styled(ColumnedFlex)`
  align-items: center;
  margin-right: 20px;
  padding: 65px 0;
  max-width: 340px;
  max-height: 340px;

  width: 100%;
  height: 100%;
  background-color: ${({ theme: { colors } }): string => colors.lightGray};
`;

const AddPhotoText = styled(Text)`
  max-width: 170px;
  width: 100%;
  margin-top: 25px;
  font-weight: 600;
  color: ${({ theme: { colors } }): string => colors.nobel};
  font-size: 31px;
  text-align: center;
  line-height: 38px;
`;

const AddInfo = styled(ColumnedFlex)`
  width: 100%;
  height: 425px;
  margin-bottom: 60px;
`;

const SearchFriends = styled(ColumnedFlex)`
  width: 100%;
`;

const InputWrapper = styled(Flex)`
  align-items: center;
  margin-bottom: 75px;
`;

const Input = styled(StyledInput)`
  margin-left: 15px;
`;

const Friends = styled(ColumnedFlex)`
  max-height: 935px;
  height: 100%;
`;

const FriendWrapper = styled(Flex)`
  justify-content: space-between;
  margin-bottom: 1px solid ${({ theme: { colors } }): string => colors.tundora};
  padding: 30px 0;
`;

const Image = styled.img`
  width: 100px;
  height: 100px;
  margin-right: 20px;
  border-radius: 50%;
`;

const Info = styled(ColumnedFlex)`
  align-self: center;
`;

const Name = styled(Title)`
  margin-bottom: 5px;
  font-weight: bold;
`;

const CheckMark = styled.div`
  align-self: center;
  height: fit-content;
  cursor: pointer;
`;

const AddFriendList = () => {
  const [friends, setFriends] = useState([]) as any;

  const [sortedFriends, setSortedFriends] = useState([]) as any;

  const [findFriendData, setFindFriendData] = useState('');

  const { userId } = useSelector(selectUser);

  const { handleModalClose } = useModal();

  const getFriends = useCallback(async () => {
    const followers = await api.getFollowers(userId);

    setFriends(followers.data);
    setSortedFriends(followers.data);
  }, [setFriends, userId]);

  useEffect(() => {
    getFriends();
  }, [getFriends]);

  const [chosenFriends, setChosenFriends] = useState([]) as any;

  const chooseFriend = useCallback(
    (friendId: any) => () => {
      if (chosenFriends.includes(friendId)) {
        setChosenFriends((state: any) => state.filter((chosenFriend: any) => chosenFriend !== friendId));
      } else {
        setChosenFriends((state: any) => [...state, friendId]);
      }
    },
    [chosenFriends, setChosenFriends],
  );

  const handleChangeAccessRule = (e: any) => {
    // console.log('ee', e);
  };

  const handleFindFollowFriend = useCallback(
    async (e: any) => {
      const name = e.target.value.toLowerCase();

      if (!name) {
        setFindFriendData(name);
        setSortedFriends(friends);

        return;
      }

      const isFirstAndLast = name.trim().split(' ').length === 2;

      const [first, last] = isFirstAndLast ? name.trim().split(' ') : [];

      setFindFriendData(name);

      const sortedUsers = isFirstAndLast
        ? friends.filter(
            (friend: any) => friend.firstName.toLowerCase() === first && friend.lastName.toLowerCase().includes(last),
          )
        : friends.filter(
            (friend: any) =>
              friend.firstName.toLowerCase().includes(name.trim()) ||
              friend.lastName.toLowerCase().includes(name.trim()),
          );

      setSortedFriends(sortedUsers);
    },
    [friends, setSortedFriends],
  );

  const onSubmit = useCallback(
    async ({ name, description, accessRule }: any) => {
      await api.addFriendList(
        userId,
        name,
        accessRule.value,
        description,
        'https://bigpikcha.ru/wp-content/uploads/2019/03/mai.jpg',
        chosenFriends,
      );
    },
    [chosenFriends, userId],
  );

  return (
    <Wrapper>
      <AddFriendListWrapper>
        <Header>
          <H1>Добавить список</H1>
          <StyledIcon type="closeCircle" width="50px" height="50px" onClick={handleModalClose} />
        </Header>

        <Flex>
          <AddPhoto>
            <Icon type="addPhoto" width="160px" height="125px" />
            <AddPhotoText>Добавить фото</AddPhotoText>
          </AddPhoto>
          <AddInfo>
            <AddFriendListForm onSubmit={onSubmit} handleChangeAccessRule={handleChangeAccessRule} />
          </AddInfo>
        </Flex>

        <SearchFriends>
          <InputWrapper>
            <StyledIcon type="search" width="31px" height="31px" />
            <Input placeholder="Поиск друзей" onChange={handleFindFollowFriend} value={findFriendData} />
          </InputWrapper>

          <Friends>
            {sortedFriends &&
              sortedFriends.map(({ id, firstName, lastName, city, country, image }: any) => (
                <FriendWrapper key={id} onClick={chooseFriend(id)}>
                  <Flex>
                    <Image src={image} />
                    <Info>
                      <Name>{firstName + ' ' + lastName}</Name>
                      <Text size="small">{city + ', ' + country}</Text>
                    </Info>
                  </Flex>
                  <CheckMark>
                    <Icon
                      type={chosenFriends && chosenFriends.includes(id) ? 'checkedMark' : 'checkMark'}
                      width="50px"
                      height="50px"
                    />
                  </CheckMark>
                </FriendWrapper>
              ))}
          </Friends>
        </SearchFriends>
      </AddFriendListWrapper>
    </Wrapper>
  );
};

export default AddFriendList;
