// libraries
import React from 'react';
import styled from 'styled-components';

// styled components
import { Flex } from 'shared/styledComponents';

// custom components
import ListItem from './ListItem';

const Wrapper = styled(Flex)`
  margin: 10px 150px 100px;
`;

interface WishListProps {
  handleModalOpen: any;
  wishLists: any;
}

const WishLists = ({ handleModalOpen, wishLists }: WishListProps) => {
  return (
    <Wrapper>
      <Flex>
        {wishLists.map((listItem: any) => {
          return <ListItem name={listItem.name} image={listItem.image} />;
        })}

        <ListItem name="Добавить список" isAddNew handleModalOpen={handleModalOpen} />
      </Flex>
    </Wrapper>
  );
};

export default WishLists;
