// libraries
import React, { useEffect } from 'react';
import styled from 'styled-components';

// styled components
import { CentredFlex } from 'shared/styledComponents';

// custom components
import AddFriendList from 'modules/AddFriendList';
import AddWishList from 'modules/AddWishList';

const Wrapper = styled(CentredFlex)<any>`
  display: ${({ isOpen }): string => (isOpen ? 'flex' : 'none')};
  position: fixed;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  top: 0;
  background-color: rgba(0, 0, 0, 0.25);
  z-index: 9999;
`;

interface ModalProps {
  title: string;
}

const Modals = ({ title }: ModalProps): JSX.Element => {
  document.body.style.overflow = 'hidden';
  document.body.style.marginRight = '15px';

  useEffect(
    (): any => (): any => {
      document.body.style.overflow = '';
      document.body.style.marginRight = '';
    },
    [],
  );

  console.log('title', title);
  return (
    <Wrapper isOpen={!!title}>
      {((): JSX.Element | null => {
        switch (title) {
          case 'addFriendList':
            return <AddFriendList />;
          case 'addWishList':
            return <AddWishList />;
          // case 'editWishList':
          //   return <EditWishList />;
          // case 'editFriendList':
          //   return <EditFriendList />;
          default:
            return null;
        }
      })()}
    </Wrapper>
  );
};

export default Modals;
