// libraries
import { AxiosResponse } from 'axios';

// api
import apiRequest from 'api/restapi';

const getWishes = (userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.post(`wishList/getWishListByUserId`, { userId });

const getAllWishLists = (userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.post(`wishList/getAllWishListsByUserId`, { userId });

const deleteWish = (userId: string, goodId: string): Promise<AxiosResponse<any>> =>
  apiRequest.post(`/wishList/deleteWish`, { goodId, userId });

export default { getWishes, deleteWish, getAllWishLists };
