// libraries
import React from 'react';
import styled from 'styled-components';

// styled components
import { Frame, Flex, CentredFlex, ColumnedFlex } from 'shared/styledComponents';
import { PaginationNumber } from 'shared/typography';

// custom components
import Popular from './components/Popular';
import SubTitle from 'components/SubTitle';
import Product from 'components/Product/MainProduct';
import Icon from 'components/Icon';
import Loader from 'components/Loader';

const Wrapper = styled.div`
  margin: 130px 0;
`;

const Ideas = styled(ColumnedFlex)`
  margin-top: 50px;
`;

const ProductsWrapper = styled(CentredFlex)`
  flex-wrap: wrap;
  justify-content: start;
`;

const PaginationWrapper = styled(Flex)`
  justify-content: space-between;
  align-items: center;
  max-width: 180px;
  margin: 80px auto;
`;

const StyledPaginationNumber = styled<any>(PaginationNumber)`
  margin-right: 20px;
  color: ${({ isChosen, theme: { colors } }) => (isChosen ? colors.supernova : colors.black)};
  cursor: pointer;

  &:nth-of-type(n + 3) {
    margin-right: 0;
  }
`;

const SliderPrevArrow = styled(Icon)`
  margin-right: 20px;
  cursor: pointer;
`;

const SliderNextArrow = styled(Icon)`
  margin-left: 20px;
  cursor: pointer;
`;

interface HomeProps {
  isIdeasLoading: boolean;
  paginationNumbers: number[];
  currentGoods: any[] | null;
  currentPage: number;
  handleChoosePage: any;
  handleAddToWish: (id: string) => void;
  handleAddToFavourites: (id: string) => void;
  wishes: any[] | null;
  favourites: any[] | null;
  allIdeas: any[] | null;
}

const PopularWrapper = styled(ColumnedFlex)`
  min-height: 700px;
`;

const LoaderWrapper = styled(CentredFlex)`
  margin-top: 150px;
`;

const Home = ({
  isIdeasLoading,
  paginationNumbers,
  currentGoods,
  currentPage,
  handleChoosePage,
  handleAddToFavourites,
  handleAddToWish,
  wishes,
  favourites,
  allIdeas,
}: HomeProps) => {
  let currentPaginationNumbers = [] as number[];

  if (currentPage === 1) {
    currentPaginationNumbers = [currentPage, ...paginationNumbers.slice(1, currentPage + 2)];
  } else if (currentPage === 2) {
    currentPaginationNumbers = [currentPage - 1, currentPage, ...paginationNumbers.slice(currentPage, currentPage + 1)];
  } else if (currentPage > 2 && currentPage < paginationNumbers.length) {
    currentPaginationNumbers = [currentPage - 1, currentPage, ...paginationNumbers.slice(currentPage, currentPage + 1)];
  } else if (currentPage === paginationNumbers.length) {
    currentPaginationNumbers = [currentPage - 2, currentPage - 1, currentPage];
  }

  return (
    <Frame>
      <Wrapper>
        <PopularWrapper>
          <SubTitle subTitle="Популярное" />
          {isIdeasLoading ? (
            <LoaderWrapper>
              <Loader />
            </LoaderWrapper>
          ) : (
            <Popular allIdeas={allIdeas} />
          )}
        </PopularWrapper>

        <SubTitle subTitle="Идеи" />

        <Ideas>
          {isIdeasLoading ? (
            <LoaderWrapper>
              <Loader />
            </LoaderWrapper>
          ) : (
            <>
              <ProductsWrapper>
                {currentGoods &&
                  currentGoods?.map((idea: any, i: any) => {
                    const isInFavourites = favourites && favourites.find((favourite: any) => favourite.id === idea.id);
                    const isInWishes = wishes && wishes.find((wish: any) => wish.id === idea.id);

                    return (
                      <Product
                        key={idea.id}
                        {...idea}
                        handleAddToFavourites={handleAddToFavourites}
                        handleAddToWish={handleAddToWish}
                        isInFavourites={isInFavourites}
                        isInWishes={isInWishes}
                      />
                    );
                  })}
              </ProductsWrapper>
              {paginationNumbers.length > 1 && (
                <PaginationWrapper>
                  {currentPage > 1 && paginationNumbers.length > 3 && (
                    <SliderPrevArrow
                      type="prevArrow"
                      width="10px"
                      height="20px"
                      onClick={handleChoosePage(currentPage - 1)}
                    />
                  )}

                  {currentPaginationNumbers.map((item: number) => (
                    <StyledPaginationNumber key={item} isChosen={item === currentPage} onClick={handleChoosePage(item)}>
                      {item < 10 ? '0' + item : item}
                    </StyledPaginationNumber>
                  ))}

                  {currentPage < paginationNumbers.length && paginationNumbers.length > 3 && (
                    <SliderNextArrow
                      type="nextArrow"
                      width="10px"
                      height="20px"
                      onClick={handleChoosePage(currentPage + 1)}
                    />
                  )}
                </PaginationWrapper>
              )}
            </>
          )}
        </Ideas>
      </Wrapper>
    </Frame>
  );
};

export default Home;
