// libraries
import React from 'react';
import styled from 'styled-components';

// styled components
import { Frame, Flex, ColumnedFlex } from 'shared/styledComponents';
import { PaginationNumber } from 'shared/typography';

// custom components
import SubTitle from 'components/SubTitle';
import Product from 'components/Product/FavouriteProduct';
import Icon from 'components/Icon';

const Wrapper = styled.div`
  margin: 130px 0;
`;

const FavouritesWrapper = styled(ColumnedFlex)`
  margin-top: 50px;
`;

const ProductsWrapper = styled(Flex)`
  flex-wrap: wrap;
  justify-content: start;
`;

const PaginationWrapper = styled(Flex)`
  justify-content: space-between;
  max-width: 180px;
  margin: 80px auto;
`;

const StyledPaginationNumber = styled<any>(PaginationNumber)`
  margin-right: 20px;
  color: ${({ isChosen }) => (isChosen ? 'yellow' : 'black')};
  cursor: pointer;

  &:nth-of-type(n + 3) {
    margin-right: 0;
  }
`;

const SliderPrevArrow = styled(Icon)`
  margin-right: 20px;
  cursor: pointer;
`;

const SliderNextArrow = styled(Icon)`
  margin-left: 20px;
  cursor: pointer;
`;

interface FavouriteProps {
  isFavouritesLoading: boolean;
  paginationNumbers: number[];
  currentFavourites: any[] | null;
  currentPage: number;
  handleChoosePage: any;
}

const Favourites = ({
  isFavouritesLoading,
  paginationNumbers,
  currentFavourites,
  currentPage,
  handleChoosePage,
}: FavouriteProps) => (
  <Frame>
    <Wrapper>
      <SubTitle subTitle="Понравилось" />

      {!isFavouritesLoading && (
        <FavouritesWrapper>
          <ProductsWrapper>
            {currentFavourites && currentFavourites?.map((idea: any, i: any) => <Product key={i} {...idea} />)}
          </ProductsWrapper>
          <PaginationWrapper>
            <SliderPrevArrow type="prevArrow" width="10px" height="20px" onClick={handleChoosePage(currentPage - 1)} />
            {paginationNumbers.map((i: number) => (
              <StyledPaginationNumber isChosen={currentPage === i} key={i} onClick={handleChoosePage(i)}>
                {i < 10 ? '0' + i : i}
              </StyledPaginationNumber>
            ))}
            <SliderNextArrow type="nextArrow" width="10px" height="20px" onClick={handleChoosePage(currentPage + 1)} />
          </PaginationWrapper>
        </FavouritesWrapper>
      )}
    </Wrapper>
  </Frame>
);

export default Favourites;
