//libraries
import React, { useCallback } from 'react';
import { Form as FinalForm, FormRenderProps } from 'react-final-form';
import styled from 'styled-components';

// utils
import { handleSubmissionErrors } from 'utils/handleSubmissionErrors';

interface FormProps {
  children: (props: FormRenderProps) => JSX.Element;
  onSubmit: any;
  initialValues?: any;
}

const StyledForm = styled.form`
  width: 100%;
`;

const Form = ({ children, onSubmit, initialValues, ...restProps }: FormProps): JSX.Element => {
  const handleSubmit = useCallback((values) => handleSubmissionErrors(onSubmit)(values), [onSubmit]);

  return (
    <FinalForm onSubmit={handleSubmit} initialValues={initialValues} {...restProps}>
      {(props: FormRenderProps): JSX.Element => (
        <StyledForm onSubmit={props.handleSubmit}>
          <>{children(props)}</>
        </StyledForm>
      )}
    </FinalForm>
  );
};

export default Form;
