// libraries
import { AxiosResponse } from 'axios';

// api
import apiRequest from 'api/restapi';

const getIdeas = (): Promise<AxiosResponse<any>> => apiRequest.get(`ideas/getAll`);

const getPopularGoods = (): Promise<AxiosResponse<any>> => apiRequest.get(`/`);

const addToFavourite = (goodId: string, userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.post(`/favouritesList/addFavouriteGood`, { goodId, userId });

const addToWish = (goodId: string, userId: string): Promise<AxiosResponse<any>> =>
  apiRequest.post(`/wishList/addWishGood`, { goodId, userId });

export default { getIdeas, getPopularGoods, addToFavourite, addToWish };
