// libraries
import React from 'react';
import { Field, FieldProps, FieldRenderProps } from 'react-final-form';
import styled from 'styled-components';

// styled components
import { Error } from 'shared/styledComponents';

const StyledError = styled(Error)`
  margin: 5px 0;
`;

const Input = (props: FieldProps<string, FieldRenderProps<string, HTMLElement>, HTMLInputElement>): JSX.Element => (
  <Field {...props}>
    {({ input, meta }: any) => (
      <>
        <input
          className={props.className}
          {...input}
          {...props}
          onChange={(e: any) => {
            input.onChange(e);

            if (props.onChange) {
              props.onChange(e.target.value, e.target.name);
            }
          }}
        />
        {(meta.touched && meta.error && <StyledError size="small">{meta.error}</StyledError>) ||
          (meta.submitError && <StyledError size="small">{meta.submitError}</StyledError>)}
      </>
    )}
  </Field>
);

export default Input;
