// libraries
import React, { useEffect, useState, useCallback } from 'react';
import { useSelector } from 'react-redux';

// custom components
import WishList from './WishList';

// api
import api from './api';

// selectors
import { selectUser } from 'modules/User/selectors';

const WishListPage = () => {
  const [isWishesLoading, setIsWishesLoading] = useState(false);

  const [allWishes, setWishes] = useState<any[] | null>(null);

  const [currentIdeas, setCurrentIdeas] = useState<any[] | null>(null);

  const [paginationNumbers, setPaginationNumbers] = useState<number[]>([]);

  const [currentPage, setCurrentPage] = useState(1);

  const { userId } = useSelector(selectUser);

  const GOODS_PER_PAGE = 16;

  const handleChoosePage = useCallback(
    (pageNumber: number) => () => {
      if (!pageNumber) return;

      setCurrentPage(pageNumber);

      const indexOfLastGood = pageNumber * GOODS_PER_PAGE;
      const indexOfFirstGood = indexOfLastGood - GOODS_PER_PAGE;

      setCurrentIdeas(allWishes?.slice(indexOfFirstGood, indexOfLastGood) || []);
    },
    [allWishes],
  );

  const getWishes = useCallback(async () => {
    setIsWishesLoading(true);

    try {
      const wishes = await api.getWishes(userId);

      setWishes(wishes.data?.goods || []);
      setCurrentIdeas(wishes.data.goods?.slice(0, GOODS_PER_PAGE) || []);

      const paginationAmount = Math.ceil(40 / GOODS_PER_PAGE);

      for (let i = 1; i <= paginationAmount; i++) {
        setPaginationNumbers((state: number[]) => [...state, i]);
      }
    } catch (err) {
      console.log('err', err);
    }

    setIsWishesLoading(false);
  }, [userId]);

  const handleDeleteWish = useCallback(
    (goodId: string) => async () => {
      try {
        await api.deleteWish(userId, goodId);

        setCurrentIdeas((state: any) => {
          const filtered = state.filter((wish: any) => wish.id !== goodId);

          const indexOfLastGood = currentPage * GOODS_PER_PAGE;
          const indexOfFirstGood = indexOfLastGood - GOODS_PER_PAGE;

          return filtered?.slice(indexOfFirstGood, indexOfLastGood) || [];
        });
      } catch (error) {
        console.log('err', error);
      }
    },
    [currentPage, userId],
  );

  useEffect(() => {
    getWishes();
  }, [getWishes]);

  return (
    <WishList
      isWishesLoading={isWishesLoading}
      paginationNumbers={paginationNumbers}
      currentGoods={currentIdeas}
      currentPage={currentPage}
      handleChoosePage={handleChoosePage}
      handleDeleteWish={handleDeleteWish}
    />
  );
};

export default WishListPage;
